                        userDTO.month = !UtilService.isEmpty(localState.primaryApplicant.employmentDetails.employmentMonth) ? UtilService.formatYearMonth(localState.primaryApplicant.employmentDetails.employmentMonth) : null;
                        userDTO.grossIncome = !UtilService.isEmpty(localState.primaryApplicant.employmentDetails.annualIncome) ? parseInt(localState.primaryApplicant.employmentDetails.annualIncome) : null;
                        if (userDTO.grossIncome === 0) {
                            userDTO.grossIncome = null;
                        }
                        userDTO.isAnnualIncomeDisabled = localState.primaryApplicant.employmentDetails.isAnnualIncomeDisabled;
                        if (localState.primaryApplicant.employmentDetails.companyAddress) {
                            userDTO.selectedCountry = localState.primaryApplicant.employmentDetails.companyAddress.country ? localState.primaryApplicant.employmentDetails.companyAddress.country : null;
                            userDTO.companyAddress = localState.primaryApplicant.employmentDetails.companyAddress.address1 ? localState.primaryApplicant.employmentDetails.companyAddress.address1 : null;
                            userDTO.city = localState.primaryApplicant.employmentDetails.companyAddress.city ? localState.primaryApplicant.employmentDetails.companyAddress.city : null;
                            userDTO.state = localState.primaryApplicant.employmentDetails.companyAddress.state ? localState.primaryApplicant.employmentDetails.companyAddress.state : null;
                            userDTO.postalCode = localState.primaryApplicant.employmentDetails.companyAddress.pinCode ? localState.primaryApplicant.employmentDetails.companyAddress.pinCode : null;
                            userDTO.mobileNo = localState.primaryApplicant.employmentDetails.companyAddress.phone ? localState.primaryApplicant.employmentDetails.companyAddress.phone : null;
                            userDTO.ext = localState.primaryApplicant.employmentDetails.companyAddress.officeExtension ? localState.primaryApplicant.employmentDetails.companyAddress.officeExtension : null;
                            userDTO.officeSTD = localState.primaryApplicant.employmentDetails.companyAddress.stdCode ? localState.primaryApplicant.employmentDetails.companyAddress.stdCode : null;
                        }
                        userDTO.designation = localState.primaryApplicant.employmentDetails.designation ? localState.primaryApplicant.employmentDetails.designation : null;
                        userDTO.selectedBusiness = localState.primaryApplicant.employmentDetails.businessNature ? localState.primaryApplicant.employmentDetails.businessNature : null;
                        userDTO.selectedOrganisation = localState.primaryApplicant.employmentDetails.organisationNature ? localState.primaryApplicant.employmentDetails.organisationNature : null;
                        userDTO.otherBusiness = localState.primaryApplicant.employmentDetails.otherBusinessNature ? localState.primaryApplicant.employmentDetails.otherBusinessNature : null;
                        userDTO.otherOrg = localState.primaryApplicant.employmentDetails.otherOrganisationNature ? localState.primaryApplicant.employmentDetails.otherOrganisationNature : null;
                        userDTO.selectedFirm = localState.primaryApplicant.employmentDetails.firmType ? localState.primaryApplicant.employmentDetails.firmType : null;
                        userDTO.companyName = localState.primaryApplicant.employmentDetails.companyName ? localState.primaryApplicant.employmentDetails.companyName : null;
                        userDTO.perfiosCompanyName = localState.primaryApplicant.employmentDetails.perfiosCompanyName ? localState.primaryApplicant.employmentDetails.perfiosCompanyName : null;
                        userDTO.changedCompanyName = localState.primaryApplicant.employmentDetails.changedCompanyName === 'Y';
