(function () {
    'use strict';
    var angular = require('angular');
    require('./card-swap.modal.component.scss')

    var modalController = function (analyticService) {
        var vm = this;

        vm.$onInit = function () {
            analyticService.buttonList.popup.event_content = "Swap card selected by customer based on annual income confirmation";
            analyticService.trackUserAction(analyticService.buttonList.popup);
        };

        /**
         * This function is triggered when we select or unselect card.
         * We push the selected card in selectedPrimaryCards array and
         *  pop the unselected card.
         */
        vm.closeOverlay = function (isOk) {
            analyticService.buttonList.buttonClick.event_content = "Swap card selected by customer based on annual income confirmation: " + isOk ? "Proceed" : "I do not wish to proceed";
            analyticService.trackUserAction(analyticService.buttonList.buttonClick);
            vm.close({
                $value: isOk
            });
        };

        vm.cancel = function () {
            vm.dismiss({
                $value: 'cancel'
            });
        };
    };

    var modalComp = angular.module('journey.module').component('cardSwapModal', {
        template: require('./card-swap.modal.component.html'),
        bindings: {
            resolve: '<',
            close: '&',
            dismiss: '&'
        },
        controller: ['analyticService', modalController]
    });
    module.exports = modalComp.name;
})();



