(function () {
    'use strict';

    var step6Service = angular.module('journey.module').service('step6Service', ['$rootScope', '$http', 'appState', 'UtilService', 'CONFIG', function ($rootScope, $http, appState, UtilService, CONFIG) {
        var self = this;

        self.setDataInStorage = function (formDetails) {
            var data = {
                "primaryApplicant": {
                    "employmentDetails": formDetails
                }
            };
            appState.mergeApplicationData(data);
        };

        self.getDataFromStorage = function () {
            return appState.getApplicationData();
        };

        self.getSelectedCardKey = function () {
            var appData = appState.getApplicationData();
            return appData.primaryApplicant.card.key;
        };

        self.getCompanyDetail = function (val) {
            var apiURL;
            if ($rootScope.integrationsActive) {
                apiURL = CONFIG.apiUrl + '/lookup/fetchcompanynames/' + val + '?hash_id=' + Math.random();
            } else {
                apiURL = 'resources/data/mock/step6_income-details/companies.json';
            }

            return $http({
                method: 'GET',
                url: apiURL,
                headers: {
                    'Content-Type': 'application/json'
                }
            }).then(function (response) {
                if (response && response.status === 200 && response.data) {
                    return response.data;
                } else if (response && response.validationErrors) { } else { }
                return;
            }, function (response) {
                return;
            });
        };


        self.sendEmploymentData = function (empData, securityParams, isSaveExit) {
            var apiURL, httpMethod;
            if ($rootScope.integrationsActive) {
                httpMethod = "POST";
                var arn = appState.getApplicationData().primaryApplicant.arn;
                apiURL = CONFIG.apiUrl + '/applicantdetails/employmentdetails/save/' + arn + '/' + isSaveExit + '?hash_id=' + Math.random();
            } else {
                httpMethod = "GET";
                apiURL = 'resources/data/mock/step6_income-details/employment_details_save.json';
            }
            return $http({
                method: httpMethod,
                url: apiURL,
                headers: {
                    'Content-Type': 'application/json'
                },
                data: JSON.stringify(empData),
                params: securityParams
            }).then(function (response) {
                if (response.status === 200 && response.data) {
                    if (response.data.success) {

                        if(response.data.salaryFlag){
                            // console.log("akshayCCS1 changes",response.data.salaryFlag);
                            // alert(response.data.salaryFlag);
                            (response.data.salaryFlag == "success")?
                            appState.setHubSalaryFlag(response.data.salaryFlag) : '';
                        }
                        
                        if (response.data.message !== 'Negative Company') {
                            empData.isAnnualIncomeDisabled = response.data.leadGenerationReason;
                        }
                        self.setDataInStorage(empData);
                    }
                    return response.data;
                }
                return {
                    success: false,
                    message: "Sorry! there seems to be some technical error."
                };
            }, function (error) {
                return {
                    success: false,
                    message: "Sorry! there seems to be some technical error."
                };
            });
        };

        self.sendLead = function (securityParams) {
            var apiURL, httpMethod;
            if ($rootScope.integrationsActive) {
                httpMethod = "POST";
                var arn = appState.getApplicationData().primaryApplicant.arn;
                apiURL = CONFIG.apiUrl + '/iccm/sendNotification/' + arn + '?hash_id=' + Math.random();
            } else {
                httpMethod = "GET";
                apiURL = 'resources/data/mock/step6_income-details/send-lead.json';
            }
            return $http({
                method: httpMethod,
                url: apiURL,
                headers: {
                    'Content-Type': 'application/json'
                },
                // data: JSON.stringify(empData),
                params: securityParams
            }).then(function (response) {
                if (response.status === 200 && response.data) {
                    return response.data;
                }
                return {
                    success: false,
                    message: "Sorry! there seems to be some technical error."
                };
            }, function (error) {
                return {
                    success: false,
                    message: "Sorry! there seems to be some technical error."
                };
            });
        };

        self.swapSelectedCard = function () {
            return $http({
                method: "GET",
                url: "resources/data/mock/step1_cards.json"
            }).then(function (response) {
                if (response && response.status === 200 && response.data) {
                    var svcCard = response.data.filter(function (card) { return card.key === 'SVC' })[0];
                    var appData = {
                        "primaryApplicant": {
                            "card": svcCard
                        }
                    };
                    appState.mergeApplicationData(appData);
                    return true;
                }
                return false;
            }, function (error) {
                return false;
            });
        };

        self.lengthOfStayYears = [];
        for (var i = 0; i <= 65; i++) {
            if (i < 2) {
                this.lengthOfStayYears.push({ value: i, label: i + " year" });
            }
            else {
                this.lengthOfStayYears.push({ value: i, label: i + " years" });
            }
        }

        self.lengthOfStayMonths = [];
        for (var i = 0; i < 12; i++) {
            if (i < 2) {
                this.lengthOfStayMonths.push({ value: i, label: i + " month" });
            }
            else {
                this.lengthOfStayMonths.push({ value: i, label: i + " months" });
            }
        }

        self.getUserDetails = function () {
            var userDTO = {},
                localState = appState.getApplicationData();
            if (localState) {
                if (localState.primaryApplicant) {
                    userDTO.empType = localState.primaryApplicant.employmentStatus ? localState.primaryApplicant.employmentStatus : null;
                    userDTO.perfiosCheck = localState.primaryApplicant.hasSkippedPerfios ? localState.primaryApplicant.hasSkippedPerfios : 'Y';
                    userDTO.perfiosCheck = userDTO.perfiosCheck === 'Y' ? false : true;
                    if (localState.primaryApplicant.employmentDetails) {
                        userDTO.year = !UtilService.isEmpty(localState.primaryApplicant.employmentDetails.employmentYear) ? UtilService.formatYearMonth(localState.primaryApplicant.employmentDetails.employmentYear) : null;
                        userDTO.month = !UtilService.isEmpty(localState.primaryApplicant.employmentDetails.employmentMonth) ? UtilService.formatYearMonth(localState.primaryApplicant.employmentDetails.employmentMonth) : null;
                        userDTO.grossIncome = !UtilService.isEmpty(localState.primaryApplicant.employmentDetails.annualIncome) ? parseInt(localState.primaryApplicant.employmentDetails.annualIncome) : null;
                        if (userDTO.grossIncome === 0) {
                            userDTO.grossIncome = null;
                        }
                        userDTO.isAnnualIncomeDisabled = localState.primaryApplicant.employmentDetails.isAnnualIncomeDisabled;
                        if (localState.primaryApplicant.employmentDetails.companyAddress) {
                            userDTO.selectedCountry = localState.primaryApplicant.employmentDetails.companyAddress.country ? localState.primaryApplicant.employmentDetails.companyAddress.country : null;
                            userDTO.companyAddress = localState.primaryApplicant.employmentDetails.companyAddress.address1 ? localState.primaryApplicant.employmentDetails.companyAddress.address1 : null;
                            userDTO.city = localState.primaryApplicant.employmentDetails.companyAddress.city ? localState.primaryApplicant.employmentDetails.companyAddress.city : null;
                            userDTO.state = localState.primaryApplicant.employmentDetails.companyAddress.state ? localState.primaryApplicant.employmentDetails.companyAddress.state : null;
                            userDTO.postalCode = localState.primaryApplicant.employmentDetails.companyAddress.pinCode ? localState.primaryApplicant.employmentDetails.companyAddress.pinCode : null;
                            userDTO.mobileNo = localState.primaryApplicant.employmentDetails.companyAddress.phone ? localState.primaryApplicant.employmentDetails.companyAddress.phone : null;
                            userDTO.ext = localState.primaryApplicant.employmentDetails.companyAddress.officeExtension ? localState.primaryApplicant.employmentDetails.companyAddress.officeExtension : null;
                            userDTO.officeSTD = localState.primaryApplicant.employmentDetails.companyAddress.stdCode ? localState.primaryApplicant.employmentDetails.companyAddress.stdCode : null;
                        }
                        userDTO.designation = localState.primaryApplicant.employmentDetails.designation ? localState.primaryApplicant.employmentDetails.designation : null;
                        userDTO.selectedBusiness = localState.primaryApplicant.employmentDetails.businessNature ? localState.primaryApplicant.employmentDetails.businessNature : null;
                        userDTO.selectedOrganisation = localState.primaryApplicant.employmentDetails.organisationNature ? localState.primaryApplicant.employmentDetails.organisationNature : null;
                        userDTO.otherBusiness = localState.primaryApplicant.employmentDetails.otherBusinessNature ? localState.primaryApplicant.employmentDetails.otherBusinessNature : null;
                        userDTO.otherOrg = localState.primaryApplicant.employmentDetails.otherOrganisationNature ? localState.primaryApplicant.employmentDetails.otherOrganisationNature : null;
                        userDTO.selectedFirm = localState.primaryApplicant.employmentDetails.firmType ? localState.primaryApplicant.employmentDetails.firmType : null;
                        userDTO.companyName = localState.primaryApplicant.employmentDetails.companyName ? localState.primaryApplicant.employmentDetails.companyName : null;
                        userDTO.perfiosCompanyName = localState.primaryApplicant.employmentDetails.perfiosCompanyName ? localState.primaryApplicant.employmentDetails.perfiosCompanyName : null;
                        userDTO.changedCompanyName = localState.primaryApplicant.employmentDetails.changedCompanyName === 'Y';
                        userDTO.officeNumberConsent = localState.primaryApplicant.employmentDetails.officeNumberConsent === 'Y';

                        userDTO.accountNumber = localState.primaryApplicant.employmentDetails.accountNumber ? localState.primaryApplicant.employmentDetails.accountNumber : null;
                        userDTO.SI = localState.primaryApplicant.employmentDetails.SI ? localState.primaryApplicant.employmentDetails.SI : null;
                    
                        // userDTO.selectedBank = localState.primaryApplicant.employmentDetails.primaryBank ? localState.primaryApplicant.employmentDetails.primaryBank : null;
                        // userDTO.branch = localState.primaryApplicant.employmentDetails.branchLocation ? localState.primaryApplicant.employmentDetails.branchLocation : null;
                        // userDTO.accountNumber = localState.primaryApplicant.employmentDetails.accountNumber ? localState.primaryApplicant.employmentDetails.accountNumber : null;
                    }
                }
            }
            return userDTO;
        };

        self.getApplicantAge = function () {
            var applicant = self.getDataFromStorage().primaryApplicant;
            var dob = applicant.dateOfBirth;
            dob = dob.split('-');
            var age = UtilService.calculateAge(dob[0], dob[1], dob[2]);
            return age;
        };

        self.getBusinessTypes = function () {
            return $http.get('resources/data/business-types.json', {
                cache: true
            }).then(function (response) {
                return response.data;
            });
        };

        self.getOrganizationTypes = function () {
            return $http.get('resources/data/organization-types.json', {
                cache: true
            }).then(function (response) {
                return response.data;
            });
        };

        self.getFirmTypes = function () {
            return $http.get('resources/data/firm-types.json', {
                cache: true
            }).then(function (response) {
                return response.data;
            });
        };
    }]);

    module.exports = step6Service.name;
})();



